.loader-wrapper {
  position: fixed;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  z-index: 1000;
  -webkit-transition: visibility 0.3s, opacity 0.3s linear;
  transition: visibility 0.3s, opacity 0.3s linear; /* Opacity for the fade-out effect. Visibility to allow interaction underneath the element. */
  visibility: visible;
  display: -webkit-flex;
  display: flex;
  background-color: #fafafa
}

.loader-wrapper--hide {
  opacity: 0;
  visibility: hidden;
}


.spinner {
  border: 0.05rem solid transparent;
  border-radius: 50%;
  display: block;
  margin: auto;
  border-top: 0.05rem solid #052fb8;
  /* border-bottom: 0.05rem solid #052fb8; */
  width: 5rem;
  height: 5rem;
  /* animation: spin 0.5s cubic-bezier(0.5, 0.6, 0.5, 1) infinite;  */
  -webkit-animation: spin 1s ease-out infinite;
          animation: spin 1s ease-out infinite;
}

.spinner::before {
  content: "";
  position: absolute;
  border: 0.05rem solid transparent;
  border-radius: 50%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  /* border-left: 0.05rem solid #052fb8;
  border-right: 0.05rem solid #052fb8; */
  border-bottom: 0.05rem solid rgb(255, 32, 117);
  width: 4.5rem;
  height: 4.5rem;
  -webkit-animation: spin2 1.5s linear infinite;
          animation: spin2 1.5s linear infinite;

}
@-webkit-keyframes spin2{
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  30% {
    -webkit-transform: rotate(400deg);
            transform: rotate(400deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spin2{
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  30% {
    -webkit-transform: rotate(400deg);
            transform: rotate(400deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loader-wrapper {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fafafa;
}

.loader {
  margin: auto;
  width: 15rem;
  height: 5rem;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  
}

.loader::after {
  content: '';
  position: absolute;
  bottom: 0;
  height: 80%;
  width: 5px;
  background-color: rgb(255, 32, 62);
  -webkit-animation: tune 3s ease-in-out infinite;
          animation: tune 3s ease-in-out infinite;
}

.loader::before {
  content: 'Tuning...';
  position: absolute;
  bottom: -2.5rem;
}

.loader span {
  background-color: rgb(75, 75, 75);
  width: 2px;
  height: 2rem;
}

.loader span:nth-child(6n + 1) {
  height: 3rem;
}

.loader span:not(:first-child) {
  margin-left: 5px;
}

@-webkit-keyframes tune {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  20% {
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }

  40% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }

  60% {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
  }

  80% {
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px);
  }
  
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes tune {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  20% {
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }

  40% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }

  60% {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
  }

  80% {
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px);
  }
  
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
